import uniIcon from '@/components/uni-icon/uni-icon.vue';
import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		uniIcon,
		wPicker 
	},
	data() {
		return {
			addrLe: true,
			timeLe: true,
			hope_job:'',
			num:2,
			cartType:'',
			cartList:[],
			resultInfo:{
				result:"配送/服务日期（必填）"
			}
		}
	},
	onLoad(options) {
		uni.setNavigationBarTitle({
		　　title:'购物车'
		})
		this.cartType = options.type
		// console.log(options.type)
		this.queryCartList('34cf8b92ad0746a9ab476735e36797e7');
	},
	onShow:function(e){
	       let pages = getCurrentPages();
	       let currPage = pages[pages.length-1];
	       if (currPage.data.hope_job==""){
	            
	        }else{
	            this.hope_job = JSON.parse(currPage.data.hope_job)
	        }
	 },
	methods: {
		//增加规格数量
		addNum:function(index){
			this.cartList[index].num=parseInt(this.cartList[index].num)+1
		},
		//减少规格数量
		minusNum:function(index){
			if(this.cartList[index].num<1){
				this.cartList[index].num = 0
			}else{
				this.cartList[index].num=parseInt(this.cartList[index].num)-1
			}
		},
		//查看购物车列表
		queryCartList:function(userCode){
			var that = this;
			uni.request({
				method: 'GET',
				url: that.websiteUrl + '/cart/queryCartList', 
				data: {
					userCode:userCode,
				},
				success: (res) => {
					if(res.data.success){
						if(that.cartType == 1){
							that.cartList = res.data.data.rows
						}else{
							that.cartList = res.data.data.rows[res.data.data.total-1]
						}
						console.log(that.cartList)
					}else{
						uni.showToast({
							icon:"none",
							title:res.data.msg
						})
					}
					
				}
			});
		},
		goAddress:function(){
			uni.showToast({
				icon:"loading",
				title:"loading..."
			})
			uni.navigateTo({
				url:"/pages/address/address"
			}) 
		},
		toggleTab:function() {
			this.$refs.picker.show();
		},
		onConfirm:function(val){
			console.log(val);
			this.resultInfo=val;
			this.$refs.picker.hide();
		}
		
	}
}
